import React, {Component} from 'react';
import { NavLink, Route } from 'react-router-dom';
import indazooLogo from '../../img/home/indazooLogo.png';
import Navbar from 'react-bootstrap/Navbar';


class MainMenu extends Component {
    
    state = {

    }

    handleMouseClick = (e) => {
        this.setState({
            aa: e.target.innerText
        })
        console.log(this.state);
    };

    handleMouseEnter = (e) => {
        e.target.style.color = '#ffc414';
    };
  
    handleMouseLeave = (e) => {
      e.target.style.color = 'black';
    };
  
    render() {
        return (
            <div id="mainGroup">
                <div id="logoBox">
                    <img className="aboutIndazooLogo" src={indazooLogo} />
                </div>
                <div id="mainMenu" >
                    <NavLink className="mainMenu" exact to="/" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>HOME</span></NavLink>
                    <NavLink className="mainMenu" exact to="/about" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='VIDEO' ? 'clickedMenu' : ''}>ABOUT</span></NavLink>
                    <NavLink className="mainMenu" exact to="/artist/kidzoo" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='ARTIST' ? 'clickedMenu' : ''}>ARTIST</span></NavLink>
                    <NavLink className="mainMenu" exact to="/shop" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='SHOP' ? 'clickedMenu' : ''}>SHOP</span></NavLink>
                    <NavLink className="mainMenu" exact to="/contact" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='CONTACT' ? 'clickedMenu' : ''}>CONTACT</span></NavLink>
                </div>
            </div>
        )
    }
  }

  export default MainMenu;