import React, {Component} from 'react';
import MainMenu from '../common/MainMenu';

class ShopBackground extends Component {
    render() {
        return (
            <div id="aboutBackground">
                <div id="subtitle">
                    # Shop
                </div>
            </div>
        )
    }
}

class Shop extends Component {
    render () {
        return (
            <>
                <MainMenu/>
                <ShopBackground/>
            </>
        );
    }
}

export default Shop;